.App {
  text-align: center;
  background-image: url("./pages/res/bg.avif");
  height: 100vh;
  width: 100vw;
  background-size: cover;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

*{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}
.App-header {
 height: 10vh;
 width: 100vw;
 display: flex;
 /* flex-direction: column; */
 gap: 40px;
 font-size: 25px;
 align-items: center;
 justify-content: center;
 list-style: none;
 text-decoration: none;
 text-align: left;
 background: #85e0fb;

}


Link{
  text-decoration: none;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
