.balloon{
    width: 100vw;
    height: 90vh;
    background: #ffffff;
    display: flex;
    flex-direction: column;
    align-items: center;

}

.bal-pict{
    width: 95%;
    height: 70%;
    background: #ffffff;
    display: flex;
    justify-content: center;
    align-items: center;
}
.bal-pict img{
    width: 10%;
    height: 20%;
    background: #ffffff;
}

@media(max-width:600px)
{
    .bal-pict img{
        width: 15%;
        height: 15%;
        background: #ffffff;
    }
}
.btn{
    height: 30%;
    width: 100%;
    background: #a5a4a4;
    display: flex;
    align-items: center;
    justify-content: center;
}
#btn{
    background: #74f98e;
    border-radius: 4px;
    width: 100px;
    height: 30px;
    font-size: 20px
}

@keyframes enlargeShrink {
    0% {
      transform: scale(1); /* Initial size */
    }
    50% {
      transform: scale(5); /* Enlarge */
    }
    100% {
      transform: scale(1); /* Back to original size */
    }
  }
  
  .animate .bal-pict img {
    animation: enlargeShrink 7s ease;
  }
  